.hero {
  background: transparent;
  padding: var(--spacing-2xl) 0;
  text-align: center;
  position: relative;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 50%, rgba(93, 217, 196, 0.15) 0%, transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(109, 201, 232, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.hero-title {
  font-size: var(--text-5xl);
  margin-bottom: var(--spacing-md);
  font-weight: 800;
  background: linear-gradient(135deg, #a0f5e8 0%, #5dd9c4 25%, #6dc9e8 50%, #7ae7d9 75%, #69f0d1 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  position: relative;
  z-index: 1;
  filter: drop-shadow(0 0 20px rgba(93, 217, 196, 0.6))
          drop-shadow(0 0 40px rgba(122, 231, 217, 0.4))
          drop-shadow(0 0 60px rgba(109, 201, 232, 0.3));
  animation: glacialPulse 4s ease-in-out infinite;
}

@keyframes glacialPulse {
  0%, 100% {
    filter: drop-shadow(0 0 20px rgba(93, 217, 196, 0.6))
            drop-shadow(0 0 40px rgba(122, 231, 217, 0.4))
            drop-shadow(0 0 60px rgba(109, 201, 232, 0.3));
  }
  50% {
    filter: drop-shadow(0 0 30px rgba(93, 217, 196, 0.8))
            drop-shadow(0 0 50px rgba(122, 231, 217, 0.6))
            drop-shadow(0 0 80px rgba(109, 201, 232, 0.4));
  }
}

.hero-title .highlight {
  background: linear-gradient(135deg, var(--secondary-color) 0%, var(--accent-coral) 50%, var(--accent-peach) 100%);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 900;
}

.hero-subtitle {
  font-size: var(--text-2xl);
  color: var(--text-secondary);
  margin-bottom: var(--spacing-md);
  font-weight: 600;
}

.hero-description {
  font-size: var(--text-lg);
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
}

.video-intro {
  background: transparent;
}

.video-wrapper {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-lg);
  align-items: center;
}

.video-container {
  position: relative;
  width: 100%;
  max-width: 1100px;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  background: transparent;
}

.video-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: var(--radius-lg);
}

.video-description {
  max-width: 600px;
  text-align: center;
}

.video-description p {
  font-size: var(--text-base);
  color: var(--text-secondary);
}

.about {
  background: transparent;
  position: relative;
  overflow: hidden;
}

.about .section-title {
  text-align: center;
  margin-bottom: var(--spacing-xl);
}

.about::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 70% 30%, rgba(93, 217, 196, 0.08) 0%, transparent 50%),
              radial-gradient(circle at 30% 70%, rgba(109, 201, 232, 0.08) 0%, transparent 50%);
  pointer-events: none;
}

.about-content {
  max-width: var(--max-width-narrow);
  margin: 0 auto;
  background: linear-gradient(135deg, var(--bg-secondary) 0%, var(--bg-tertiary) 100%);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-lg);
  padding: var(--spacing-2xl);
  box-shadow: var(--shadow-lg), 0 0 40px rgba(93, 217, 196, 0.1);
  position: relative;
  z-index: 1;
}

.about-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color), var(--accent-teal));
  border-radius: var(--radius-lg) var(--radius-lg) 0 0;
}

.about-text p {
  font-size: var(--text-lg);
  line-height: 1.8;
  margin-bottom: var(--spacing-md);
  color: var(--text-primary);
}

.about-text p:first-of-type::first-letter {
  font-size: 3.5em;
  font-weight: 700;
  float: left;
  line-height: 0.9;
  margin-right: 0.1em;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.featured-posts {
  background: transparent;
}

.posts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-lg);
}

.post-card {
  background-color: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  padding: var(--spacing-lg);
  transition: all var(--transition-base);
  box-shadow: var(--shadow-sm);
}

.post-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg), 0 0 20px rgba(93, 217, 196, 0.3);
  border-color: var(--primary-color);
}

.post-title {
  font-size: var(--text-xl);
  margin-bottom: var(--spacing-sm);
  font-weight: 700;
  color: var(--text-primary);
}

.post-excerpt {
  font-size: var(--text-base);
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: var(--spacing-md);
}

.post-link {
  display: inline-flex;
  align-items: center;
  font-size: var(--text-base);
  color: var(--secondary-color);
  font-weight: 600;
  transition: all var(--transition-fast);
}

.post-link:hover {
  color: var(--accent-coral);
  transform: translateX(4px);
}

@media (max-width: 768px) {
  .hero {
    padding: var(--spacing-xl) 0;
  }

  .hero-title {
    font-size: var(--text-4xl);
  }

  .hero-subtitle {
    font-size: var(--text-xl);
  }

  .posts-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: var(--text-3xl);
  }

  .hero-subtitle {
    font-size: var(--text-lg);
  }

  .hero-description {
    font-size: var(--text-base);
  }
}
